html {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
body {
  height: 100vh;
  background-image: radial-gradient( circle farthest-corner at 92.3% 71.5%,  rgba(83,138,214,1) 0%, rgba(134,231,214,1) 90% );
}
h2 {
  margin-bottom: 0;
}
.main {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
canvas {
  border: 1px solid blue;
}
.menu {
  background-color: transparent;
  border: 2px solid lightgreen;
  width:  460px;
  margin: 0 auto;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 20px;
  @media (min-width: 600px) {
    width: 560px;
    padding: 20px;
  }
}
// sound btn
.sound {
  width: 30px;
  height: 30px;
  background-image: url('../assets/mute.png');
  background-repeat:  no-repeat;
  background-position: center center;
  background-size:  contain;
  margin-top: 10px;
  padding: 10px;
  border: 0;
  background-color: transparent;
  transition: all ease 0.1s;
}
.on {
  background-image: url('../assets/sound.png');
}
.start-btn,
.dropbtn {
  outline: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 1;
  border-radius: 500px;
  transition: all ease 0.2s;
  border: 2px solid #DE3163;
  letter-spacing: 2px;
  min-width: 160px;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  padding: 10px 16px;
  color: black;
  background-color: transparent;
  height: 48px;
  &:hover {
      color: #fff;
      background-color: #DE3163;
  }
  &:active {
  background-color: #990043;
}

}

.container {
  margin: 0 auto;
  display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
.settings {
  display: flex;
  flex-direction: row;
  gap: 20px;
  @media (min-width: 600px) {
    gap: 30px;
  }
}
.gamestats {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
// bombs value
.mines {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .value {
    border-radius: 20px;
    width: 20px;
    border: 2px solid #DE3163;
    background-color: transparent;
    padding: 5px;
    text-align: center;
  }
}
input[type="range"] {
  width: 100px;
}
//dropdown menu
 .dropbtn {
 min-width: 100px;
 }
 select {
  background-image: none;
  border:none;
  -moz-appearance: none;
   -webkit-appearance: none;
   appearance: none;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: transparent;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {background-color: #f1f1f1}

.dropdown:hover .dropdown-content {
  display: block;
}
// stats
.gamestats {
  margin-top: 20px;
  text-transform: uppercase;
  font-weight: 700;
}
// modal
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}


.modal-content {
  background-color: #F5EEF8 ;
  margin: 10% auto;
  padding: 20px;
  border: 4px solid #633974;
  border-radius: 10px;
  width: 50%;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 28px;
}
.modal-img {
  width: 300px;
  height: 300px;
}
.close {
  color: #633974;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #154360;
  text-decoration: none;
  cursor: pointer;
}